<template>
  <div >
    <div class="mhead" v-if="!hideHead">
      <ul>
        <li>
          <text></text>
        </li>
      </ul>
    </div>
    <div class="body">
      <div class="signal">
        <div class="picsin">
          <div class="picview">
            <img :src="imgArray[index]"/>
          </div>
        </div>
        <div class="textsin">
          <dl>
            <dt>
              <span>{{message}}</span>
              <br/>
              <span v-if="isdone && isfail && !printFail && failCount<writeRetryTime" style="color:red;">{{msg}}</span>
            </dt>
          </dl>
        </div>
      </div>
      <div class="btnpanel">
        <dl class="sucbtns" v-if="isdone && printFail">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="toprint()"></button>
          </dd>
        </dl>
        <dl class="sucbtns" v-if="isdone && isfail && !printFail && failCount<writeRetryTime">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="writecard()"></button>
          </dd>
        </dl>
        <dl class="sucbtns" v-if="isdone && isbackerro">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="writecard()" ></button>
          </dd>
        </dl>
      </div>
    </div>
    <div class="foot" style="margin-top: -10%;" v-if="checkResult">
      <text></text>
    </div>
    <div class="foot" v-if="!checkResult" @click="checkUser()" style="margin-top: -10%;">
      <text ></text>
    </div>
    <advert-pop  :show = "show" :advertpath="advertPath" :timeout="countDown"  :msg="message" ></advert-pop>
  </div>
</template>
<script>
import co from 'co'
import Vue from 'vue'


//todo
let TimeOutObj
let InterValObj

let timeoutAutoBack = function *(self) {
  if(Vue.PosUtil.posParam.AutoBack){
    TimeOutObj = window.setInterval(() => {
      self.back('tomain')
    },  Vue.PosUtil.posParam.AutoBackTime)
  }else {
    console.log('')
  }
}

let writeBack =function * (self){
  console.log("",self)
  self.backData.f_write_card = self.isWrited
  self.message = ''
  return  self.$resetpost(Vue.ProxyUrl + '/rs/logic/'+self.$PosUtil.posParam.revenue+'writeBack', {data: self.backData},
    {resolveMsg: null, rejectMsg: null,aoteEncrypt:Vue.PosUtil.posParam.EncryType,encryKey: Vue.PosUtil.posParam.EnceyKey})
}

let writeBackGen = function * (self){
  let  writebackret
  try {
    writebackret = yield co(writeBack(self))
    console.log("writebackret",writebackret)
    if(!writebackret) {
      self.message = ''
      self.isbackerro = true
      return
    }
    console.log("")
    self.showAdvert ()
  }catch (error){
    console.log(error)
    if(!writebackret){
      self.message = ''
      self.isbackerro = true
    }
  }
}

let printGen  = function * (self) {
  self.printFail = false
  self.checkResult = false
  self.message = ''
  let pres =  self.print()
  console.log('',pres)
  if (pres.code === 200) {
    console.log('',pres)
    self.index = 0
    self.message = ''
    // self.back('tomain')
    window.setTimeout(function () {
      self.back('tomain')},5000)
  } else {
    console.log('',pres)
    self.index = 2
    self.message = '()POS'
    self.printFail = true
    return
  }
}

let getParamGen = function * (self) {
  let condition = ''
  if (self.$PosUtil.posParam.filiale.hasFiliale) {
    condition = self.$PosUtil.posParam.filiale.f_filialeid
  }
  return self.$resetpost(Vue.ProxyUrl + '/rs/logic/'+self.$PosUtil.posParam.revenue+'stairCard', {data: {f_card_id: self.cardInfo.CardID, f_alias: self.cardInfo.Factory,id:self.backData.id, f_filialeid:condition}
  }, {resolveMsg: null, rejectMsg: '',aoteEncrypt:Vue.PosUtil.posParam.EncryType,encryKey: Vue.PosUtil.posParam.EnceyKey})
}


// 
let sellGasGen = function * (self) {

  self.message = '...'
  self.isdone = false
  self.isfail = false
  let cardParams
  let writeRes
  self.index = 0
  try {
    cardParams =yield co(getParamGen(self))
    console.log('',cardParams)
    if (!cardParams) {
      self.index = 1
      self.message = ''
      self.isWrited = ''
      self.isdone = true
      self.isfail = true
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
    let params = self.$PosUtil.writeCardParams(cardParams, self)
    console.log('',params)
    self.message = '...'
    let readinfo = self.$AndroidPlugin.read()
    console.log('',self.cardInfo)
    console.log('',readinfo)
    // self.cardInfo.CardID = 1
    if (readinfo.code !== 200 || readinfo.msg.CardID !== self.cardInfo.CardID) {
      self.index = 1
      self.message = ',,'
      self.isWrited = ''

      self.isdone = true
      self.isfail = false
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
    // console.log('')
    self.message = '...'
    writeRes = self.$AndroidPlugin.sell(params)
    // console.log('writeRes==',writeRes)
    self.backData = Object.assign({}, writeRes.msg, self.backData)
    console.log('backData',self.backData)
    if (writeRes.code === 200) {
      self.message = '...'
      self.isWrited = ''
      self.isdone = true
      self.isfail = false
      co(writeBackGen(self))
      return
    } else {
      self.index = 1
      var errorMsg = writeRes.msg ? (!writeRes.msg.Err ? writeRes.msg.Exception : writeRes.msg.Err) : ''
      self.message = errorMsg + ' '
      self.isWrited = ''
      self.isdone = true
      self.isfail = true
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
  } catch (error) {
    self.index = 1
    self.message = '!'
    self.isWrited = ''
    self.isdone = true
    self.isfail = true
    if (self.failCount >= self.writeRetryTime){
      co(writeBackGen(self))
    }else {
      co(timeoutAutoBack(self))
    }
  }
}

export default {
  title: '',
  data () {
    return {
      index: 0,
      checkResult: true,
      isbackerro:false,
      isWrited: '',
      isRefund: '',
      isfail: false,
      writeRetryTime: 0,
      failCount: 0,
      Advert: {
        hasAdvert: false,
        imgName:"",
        outTime: 0
      },
      countDown: 0,
      advertPath: "",
      isdone: false,
      printFail:false,
      advertName: "",
      message: '...',
      msg: '',
      backData:{},
      // info: ',!',
      imgArray: [
        // require('../../image/loading.gif'),
        require('../../image/success.png'),
        require('../../image/fail.png'),
        require('../../image/fail2.png')
      ],
      show: false

    }
  },
  props: ['userInfo', 'cardInfo', 'stairPrice','hideHead'],
  methods: {
      tomain () {
          this.$back('tomain')
          // HostApp.backHome()
      },
      checkUser () {
        this.$goto('init',{},'self', this.back)
      },
      back (str) {
        window.clearInterval(InterValObj) // 
        this.show = false
        that.$goto('main')
      },
     isprint() {
       co(timeoutAutoBack(this))
       const that = this
       this.$showMessage('""""', ['confirm', 'cancel']).then((res) => {
         if (res === 'confirm') {
           that.doprint()
         }else{
           that.$goto('main')
         }
       })
     },
    writecard () {
      this.failCount += 1
      if(this.TimeOutObj){
        window.clearInterval(this.TimeOutObj)
      }
      let sellgas = sellGasGen(this)
      return co(sellgas)
    },
    toPrint(){
      if(!Vue.PosUtil.posParam.autoPrint){
        this.isprint()
      }else {
        this.doprint()
      }
    },
    showAdvert () {
      console.log('',this.Advert && this.Advert.hasAdvert)
      if(this.Advert && this.Advert.hasAdvert){
        if(!this.isfail){
          this.message = ''
        }else {
          this.message = ''
        }
        console.log('')
        this.show = true
        InterValObj = window.setInterval(this.outOfPage, 1000)
      }else {
        console.log('')
        this.toPrint()
      }
    },
    doprint(){
      let reprints = printGen(this)
      return co(reprints)
    },

    print () {
      // 
      console.log("this.stairPrice", this.stairPrice)
      var printcontent = []
      // 
      var comName = this.$PosUtil.posParam.companyName

      printcontent.push({type: 3, text: comName+'', fontsize: 3, isbold: true, align: 'center'
      })
      printcontent.push({type: 3, text: '----------------------------', fontsize: 3, isbold: true, align: 'center'})
      printcontent.push({type: 3, text: '' + this.userInfo.f_userinfo_code, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '' + this.userInfo.f_user_name, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '' + this.userInfo.f_address, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '   ' + this.stairPrice.f_pregas + '', fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '   ' + this.stairPrice.f_preamount + '', fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '' + this.$PosUtil.getNowDate('yyyy-MM-dd HH:mm:ss'), fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '  ' + this.stairPrice.id, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '' + this.isWrited, fontsize: 2, isbold: true, align: 'left'})
      // printcontent.push({type: 3, text: '' + this.stairPrice.f_operator, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '----------------------------', fontsize: 3, isbold: true, align: 'center'})
      // printcontent.push({type: 3, text: 'POS', fontsize: 2, isbold: true, align: 'left'})
      // 
      printcontent.push({type: 4, value: 5, unit: 'pixel'})
      return this.$AndroidPlugin.print(printcontent)
    },
    outOfPage () {
      console.log('',this.countDown)
      if (this.countDown > 0) {
        --this.countDown
      } else {
        console.log('')
        window.clearInterval(InterValObj)
        this.show = false
        this.back('tomain')
      }
    }
  },
  ready () {
    console.log("")
    console.log(this.stairPrice)

    this.backData.f_user_id = this.userInfo.f_user_id
    this.backData.id = this.stairPrice.id
    this.backData.f_terminal_num = Vue.PosUtil.Sn
    this.writeRetryTime = this.$PosUtil.posParam.writeCardRetry
    this.Advert =  this.$PosUtil.posParam.Advert
      console.log(typeof this.Advert)
    if(typeof this.Advert!='undefined'){
        this.countDown = this.$PosUtil.posParam.Advert.outTime
        console.log("Advert",this.countDown)
        this.advertPath = require(`../../image/${this.$PosUtil.posParam.Advert.imgName}`)
    }
    this.writecard()
  }
}
</script>



